$(function () {
  $(".it-bottom-navscroll ul li a[href^='#']").on('click', function (e) {

    e.preventDefault();

    var hash = this.hash;

    // animate
    $('html, body').animate({
      scrollTop: $(hash).offset().top - 150
    }, 600, function () {
      if (history.pushState) {
        history.pushState(null, null, hash);
      } else {
        location.hash = hash;
      }

    });
    //close window on click
    $(overlay).trigger('click');
  });

  // navscroll back function
  $(backbutton).click(function (event) {
    $(overlay).trigger('click');
    $(this).fadeOut();
    event.preventDefault();
  });

  // navscroll item on scroll
  $(window).on('scroll', function () {
    var scrollDistance = $(window).scrollTop();
    // Assign active class to nav links while scolling
    $('.it-page-section').each(function (i) {
      if ($(this).position().top <= scrollDistance) {
        $('.it-navscroll-wrapper .menu-wrapper a.active').removeClass('active');
        $('.it-navscroll-wrapper .menu-wrapper a').eq(i).addClass('active');
        var parentsection = $('.it-navscroll-wrapper .menu-wrapper a').eq(i).closest('ul').prev('a');
        var parentparentsection = $(parentsection).closest('ul').prev('a');
        $(parentsection).addClass('active');
        $(parentparentsection).addClass('active');
        var textContent = $('.it-navscroll-wrapper .menu-wrapper a').eq(i).find('span').text();
        var $btn = $('.it-bottom-navscroll .custom-navbar-toggler');
        var $icon = $btn.find('span.it-list');
        $btn.text(textContent);
        $btn.prepend($icon);
      }
    });
  }).scroll();
});

$('.go-back').on('click', function () {
  javascript:window.history.go(-1);
  return false;
});

function notificationShow(notificationTarget,notificationTimeOut) {

	if ($('#'+notificationTarget).hasClass('dismissable')) {
		//dismissable
		$('#'+notificationTarget).fadeIn(300);
	} else {
		//standard (timeout)
		$('#'+notificationTarget).fadeIn(300);
		if (typeof notificationTimeOut == "number") {
			//timeout set by parameter
			var timeToFade = notificationTimeOut;
		} else {
			//timeout default value 7s
			var timeToFade = 7000;
		}
		//fadeout
		setTimeout(function(){
			$('#'+notificationTarget).fadeOut(100);
		}, timeToFade);
	}
}

//dismissable close button
$('.notification-close').click(function(){
	$(this).closest('.notification').fadeOut(100);
});

var $form = $('.upload-dragdrop:not(.success)');
var droppedFiles = false;

$form.on('drag dragstart dragend dragover dragenter dragleave drop', function (e) {
  e.preventDefault();
  e.stopPropagation();
}).on('dragover dragenter', function () {
  $(this).addClass('dragover');
}).on('dragleave dragend drop', function () {
  $(this).removeClass('dragover');
}).on('drop', function (e) {
  //change drag and drop state
  $(this).addClass('loading');
  //init upload circular loader
  $(this).find('.upload-progress').circularloader({
    backgroundColor: "#ffffff",//background colour of inner circle
    fontColor: "#000000",//font color of progress text
    fontSize: "40px",//font size of progress text
    radius: 130,//radius of circle
    progressBarBackground: "transparent",//background colour of circular progress Bar
    progressBarColor: "#0073e6",//colour of circular progress bar
    progressBarWidth: 96,//progress bar width
    progressPercent: 0,//progress percentage out of 100, start with 0
  });
});

