$(function () {
  $('.bootstrap-select > select').selectpicker().on("changed.bs.select", function () {
    $('.dropdown-menu li.selected').find('input[type="checkbox"]').prop('checked', true)
    $('.dropdown-menu li:not(.selected)').find('input[type="checkbox"]').prop('checked', false)
  });
});

$(function () {

  $('.rating.rating-label input[type=radio]').click(function (event) {
    var stars = $(this).val();
    var starsLabel = "stelle";
    if (stars == 1) {
      starsLabel = "stella";
    }
    $(this).closest('.rating-label').find('legend span:not(.sr-only)').text(stars + ' ' + starsLabel);
  });

});

function dimmerShow(target) {

	if ( $(target).css('display') != 'flex' ) {
		$(target).css("display", "flex").hide().fadeIn(200);
	}

}
function dimmerHide(target) {
	$(target).fadeOut(200);
}
